<template lang="html">
<div class="">
  <div class="goods">
    <div class="menu-wrapper" ref=menuWrapper>
      <ul>
        <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="sellMenu(index,$event)">
          <span class="text border-1px">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]">
            </span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref=foodsWrapper>
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
              <div class="icon">
                <img width="57px" height="57px" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol v-on:cart-add="cartAdd" :food="food"></cartcontrol><!--传入food-->
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
  </div>
  <food :food="selectedFood" ref="food"></food>
</div>
</template>
<script>

import BScroll from 'better-scroll'
// let scroll = new BScroll('.wrapper')
import shopcart from '../../components/shopcart/shopcart'
import cartcontrol from '../../components/cartcontrol/cartcontrol'
import food from '../../components/food/food'
import data from '../../../data.json'
const ERR_OK = 0

export default{
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return{
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  created() {
    this.classMap = ['decrease','discount','special','invoice','guarantee',],
    this.goods = data.goods;
    this.$nextTick(() => {
      this._initScroll();
      this._calculateHeight();
    });

    // this.$http.get('/api/goods').then(res => {
    //   res = res.body
    //   if(res.errno === ERR_OK){
    //     this.goods = res.data;
    //     // console.log(this.goods);
    //     this.$nextTick(() => {
    //       this._initScroll();
    //       this._calculateHeight();
    //     });
    //   }
    // });
  },
  computed: {
    currentIndex() {
      for(let i=0; i< this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i+1];
        if(!height2 || this.scrollY >= height1 && this.scrollY <height2) {
          return i;
        }
      }
      return 0
    },
    selectFoods() {//遍历goods
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  methods: {
    sellMenu(index,event) {
      if(!event._constructed){
        return;
      }
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let el = foodList[index];
      this.foodsScroll.scrollToElement(el,300);
      // console.log(index);
    },
    selectFood(food, event) {
      if(!event._constructed){
        return;
      }
      this.selectedFood = food;
      this.$refs.food.show(); //这样就拿到子组件了
    },
    cartAdd(target) {//下划线开始的方法是私有的，一般不调用
      //拿到target(DOM对象)之后，将其传入shopcart组件中_drop(target){}方法，
      //此处用this.$refs调用子组件，访问dom时用的是ref="menuWrapper"
      this.$nextTick(() => {//回调函数异步执行，两个动画效果就不会卡顿了
        this.$refs.shopcart.drop(target)
      });
    },
    _initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      })

      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3 //BScroll滚动时，能实时告诉我们滚动的位置，类似探针的效果
      });

      this.foodsScroll.on('scroll',(pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for(let i=0; i<foodList.length; i++){
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height)
      }
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  },
  // events: {
  //   'cart-add'(target) {
  //     this._drop(target);
  //   }
  // }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin"

  .goods
    display: flex
    position: absolute
    top: 174px
    width: 100%
    bottom: 46px
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        line-height: 14px
        /* margin: auto 12px */
        padding: 0 12px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #ffffff
          font-weight: 700
          .text
            border-none()
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.guarantee
            bg-image('guarantee_3')
          &.invoice
            bg-image('invoice_3')
          &.special
            bg-image('special_3')
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          border-1px(rgba(7,17,27,0.1))
          font-size: 12px

    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        font-size: 12px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        color: rgb(147,153,159)
        background: #f3f5f7
      .food-item
        display: flex
        margin: 18px
        padding-bottom: 18px
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none()
          margin-bottom: 0
        .icon
          flex: 0 0 57px
          margin-right: 10px
        .content
          flex: 1
          .name
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7,17,27)
          .desc, .extra
            line-height: 10px
            font-size: 10px
            color: rgb(147,153,159)
          .desc
            margin-bottom: 8px
          .extra
            .count
              margin-right: 12px

          .price
            font-weight: 700
            line-height: 24px
            .now
              color: rgb(240,20,20)
              font-size: 14px
            .old
              font-size: 10px
              margin-left: 8px
              color: rgb(147,153,159)
              text-decoration:line-through
          .cartcontrol-wrapper
            position: absolute
            right: 0
            bottom: 12px



</style>



